export default {
	data() {
		return {
			useUser:this.user,
			name:''
		}
	},
	onLoad(options) {
		
	},
	methods: {
		wc:function(){
			if(this.name.length>0){
				var that = this;
				let usurl2=that.url1+"/dinghai/user/update";
				uni.showLoading({
					title: '正在修改',
					mask: true
				});
				uni.request({
				    url: usurl2,
					header:{
						"Content-Type" : "application/x-www-form-urlencoded"
					},
					method:'POST',
				    data: {
						uid:that.useUser.uid,
						name:that.name,
						sex:that.useUser.sex
					},
				    success: (res) => {
						if(res.statusCode==200){
							uni.showToast({
								title:'修改成功'
							})
							that.useUser.name=that.name;
							uni.navigateBack({
								
							})
						}else{
							uni.showToast({
								title: '您的网络打了个盹！',
								icon:"none"
							});
						}
				    },
					fail : function(err){
						console.log(err)
						uni.showToast({
							title: '您的网络打了个盹！',
							icon:"none"
							});
						}
					})
			}
		},
		back : function(){
		   uni.navigateBack({
		
		   });
		}
	}
}
